<template>
    <el-table :data= this.recv_data height="250" style="width: 100%">
      <el-table-column prop="course_id" label="课程代号" sortable width="180" />
      <el-table-column prop="course_name" label="课程名称" sortable width="180" />
      <el-table-column prop="credit" label="学分" sortable />
      <el-table-column prop="score" label="成绩" sortable />
    </el-table>
</template>



<script>
import student_api from '@/api/student/student-api'
import student_function from '@/api/student/student-function'

export default{
    data(){
        return{
            user_id:"",
            recv_data:[],

        }
    },
    mounted() {
    this.user_id = this.$route.query.user_id
    student_function.身份校验检查(this,this.user_id)
    student_api.获取已选课程({user_id:this.user_id}).then(resp=>{
        if(resp.status_code == 200){
            this.recv_data = resp.data
            this.recv_data.forEach(course=>{
            if(course.score == -1){
                course.score = "未出分"
            }
        })
        }
        else{
            student_function.Alert_Error(this,resp.data)
        }

    })
  },


}</script>



